.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.grid-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  /* width: max(300px,30%); */
  /* max-width: 30%; */
}

.row {
  display: flex;
}

.square {
  width: 14.2%  ;
  /* height: 50px; */
  aspect-ratio: 1;
  border: 1px solid #000;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px;
  background-color: darkred;

  &.valid{
    background-color: red;
  }
}

.circle {
  width: 70%;
  /* height: 30px; */
  aspect-ratio: 1;
  border-radius: 50%;
  border-color: yellow;
  border: 2px solid orange;
  cursor: pointer;

  &.has-pin {
    background-color: yellow;
  }

  &.selected {
    border-color: white;
  }
}

button.reset {
  /* display: flex; */
  /* text-align: center; */
  /* width: 50%; */
  font-size: 150%;
  border-radius: 0px;
  border: 2px solid darkred;
  color: yellow;
  background-color: red;
  cursor: pointer;
  flex: 1;
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  max-width: 100vw;
  /* width: max(400px,100%); */
  margin: 0px auto;
  gap: 10px;
}

.bottombar{
  display: flex;
  width: 100%;
  align-items: center;
}

.score {
  flex: 1;
  color: white;
  font-size: 150%;
}

body,html {
  background-color: green;
  height: 100%;
  /* margin: 0; */
  display: flex;
  justify-content: center; /* Horizontally center content */
  align-items: center; /* Vertically center content */
}


